/* Thumbnail Stuff */
.codecontent .project-title{
  margin-top:20px;
}
.project-code{
  position: relative;
}
.content {
  -webkit-transition: .5s ease;
  transition: .5s ease;
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: left;
}
.codecontent{
  text-align: left;
  -webkit-transition: none;
  transition: none;
  opacity: 1;
}
.buttons{
  position: relative;
  left: 50%;
  top:-4%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  border-radius: 20px;
}
.buttons a{
  -webkit-transition: .5s ease;
  transition: .5s ease;
  opacity: 0;
  background-color:rgb(255, 242, 242);
  color: rgb(204, 111, 111);
  font-size: 16px;
  padding: 12px ;
  display: block;
  width: 88%;
}
.project:hover .buttons a{
  opacity: 1;
}

.project-title{
  color: rgb(204, 111, 111);
}
.project-description{
