.sticky{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.sticky>nav {
  position: absolute;
  align-self: start;
  margin-top: 20px;
}
.section-nav{
    position: fixed;
    z-index: 1;
    right: 25px;
    overflow-x: hidden;
    padding-left: 0;
    border-left: 1px solid rgba(0,0,0,.1);
    max-width: 200px;
    width: 90%;
}
.section-nav a{
  font-size: 14px;
  color: rgb(113, 113, 113);
}
.section-nav a:hover{
  font-size: 14px;
  color:rgb(94, 24, 24);
}
.section-nav li{
  list-style-type:none;
  padding-bottom: 8px;
}
.section-nav ul{
  -webkit-padding-start:10px;
          padding-inline-start:10px;
}
@media only screen and (max-width: 1479px){
.section-nav {
