@import url("https://fonts.googleapis.com/css2?family=Gotu&display=swap");

.titledesc h3 {
  margin-top: 10px;
  font-family: "Gotu", sans-serif;
  text-align: left;
  font-size: 150%;
  margin-bottom: 10px;
}
.titledesc #andmore {
  text-align: right;
}
.headerrow {
  display: flex;
  max-width: 1100px;
  margin: auto;
  justify-content: center;
  margin-bottom: 40px;
  margin-top: 40px;

  /* background-image: linear-gradient(to bottom right, #fff6f5, #ffffff);  */
}
.headerrow img {
  width: 140px;
  height: 140px;
  margin-right: 2vw;
}
.titledesc {
  width: 770px;
}
.bodytextwide {
  padding-left: 10%;
  padding-right: 10%;
}
.bodytextwide .project {
  margin-left: 20px;
  margin-right: 20px;
}
.smalltitle p {
  margin-top: 0px;
  margin-bottom: 10px;
  font-size: 1.02em;
  text-align: left;
  font-family: "Arial", sans-serif;
}
@media (max-width: 900px) {
  .headerrow img {
    display: none;
  }
  .headerrow {
    margin: 0px;
  }
}
